<template>
    <div class="wl-txtlinkhref">
        <div class="h-headtitle">AddressEdit 地址编辑</div>
        <div class="h-blocks">
            <div class="h-title">介绍</div>
            <div class="h-main">
                收货地址编辑组件，用于新建、更新收货地址。。
            </div>
        </div>

        <div class="h-blocks">
            <div class="h-title">基础用法</div>
            <div class="hb-a">
                <div style="width: 350px;border:1px solid #ccc;padding:10px;border-radius: 10px;height: 350px;">
                    <AddressEdit
                            :addressInfo="addressInfo"
                            @save="save"
                    ></AddressEdit>
                </div>

            </div>
            <div class="hb-code">
                <div class="hb-cmain">
                    <div v-highlight>
                        <pre>
                            <code>
                                &lt;AddressEdit
                                        :addressInfo="addressInfo"
                                        @save="save"
                                &gt;
                                &lt;/AddressEdit&gt;
                                 data() {
                                    return {
                                        addressInfo:{
                                            id:'',
                                            name:"张三",
                                            tel:'15556666768',
                                            province:'江苏省',
                                            city:'南京市',
                                            county:'玄武区',
                                            addressDetail:'钟灵街50号'
                                        }
                                    }
                                },
                                methods: {
                                    //保存回调
                                    save(d){
                                        alert('保存');
                                        console.log(d);
                                    }
                                },
                            </code>
                        </pre>
                    </div>
                </div>
            </div>
            <div class="hb-b view-code-btn">查看代码</div>
        </div>

        <div class="h-blocks">
            <div class="h-title">属性</div>
            <div class="hb-a">
                <el-table
                        :data="tableData"
                        style="width: 100%">
                    <el-table-column
                            prop="param"
                            label="参数"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="类型">
                    </el-table-column>
                    <el-table-column
                            prop="defaultVal"
                            label="默认值">
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">Events</div>
            <div class="hb-a">
                <el-table
                        :data="tableDataEvent"
                        style="width: 100%">
                    <el-table-column
                            prop="eventName"
                            label="事件名"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="backParams"
                            label="回调参数">
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div class="h-blocks">
            <div class="h-title">Address 数据结构</div>
            <div class="hb-a">
                <el-table
                        :data="tableDataAddress"
                        style="width: 100%">
                    <el-table-column
                            prop="key"
                            label="键名"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="desc"
                            label="说明"
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="type"
                            label="类型">
                    </el-table-column>
                </el-table>
            </div>
        </div>

    </div>
</template>

<script>
    import AddressEdit from '@/components/template/AddressEdit.vue'

    export default {
        name: 'wladdressedit',
        data() {
            return {
                addressInfo:{
                    id:'',
                    name:"张三",
                    tel:'15556666768',
                    province:'江苏省',
                    city:'南京市',
                    county:'玄武区',
                    provinceArea:'江苏省南京市玄武区',
                    addressDetail:'钟灵街50号'
                },
                tableData: [
                    {
                        param: 'addressInfo',
                        desc: '地址对象数据',
                        type: '{}',
                        defaultVal: '',
                    }
                ],
                tableDataEvent: [
                    {
                        eventName: 'save',
                        desc: '点击保存按钮时触发',
                        backParams: 'obj: 地址对象',
                    }
                ],
                tableDataAddress:[
                    {
                        key:'id',
                        desc:'每条地址的唯一标识',
                        type:'number | string'
                    },
                    {
                        key:'name',
                        desc:'收货人姓名',
                        type:'string'
                    },
                    {
                        key:'tel',
                        desc:'收货人手机号',
                        type:'number | string'
                    },
                    {
                        key:'province',
                        desc:'省份',
                        type:'string'
                    },
                    {
                        key:'city',
                        desc:'地市',
                        type:'string'
                    },
                    {
                        key:'county',
                        desc:'区县',
                        type:'string'
                    },
                    {
                        key:'provinceArea',
                        desc:'省市区',
                        type:'string'
                    },
                    {
                        key:'addressDetail',
                        desc:'详细地址',
                        type:'string'
                    }
                ]
            }
        },
        mounted() {
            this.doCode();
        },
        methods: {
            //一进来需要执行的
            doCode() {
                $('.view-code-btn').click(function () {
                    $(this).parent().find('.hb-code').slideToggle();
                });
            },
            //保存回调
            save(d){
                alert('保存');
                console.log(d);
            }

        },
        components: {
            AddressEdit
        }
    }
</script>
<style scoped>

</style>



