<template>
    <div class="address">
        <div class="edit">
            <div class="ed-lis">
                <div class="ed-lis-key">收货人</div>
                <div class="ed-lis-val"><input placeholder="请输入收货人" v-model="info.name" /></div>
            </div>
            <div class="ed-lis">
                <div class="ed-lis-key">手机号码</div>
                <div class="ed-lis-val"><input placeholder="请输入手机号码" type="tel" maxlength="11" v-model="info.tel" /></div>
            </div>
            <div class="ed-lis">
                <div class="ed-lis-key">省市区</div>
                <div class="ed-lis-val"><input placeholder="请选中省市区" v-model="info.provinceArea"/></div>
            </div>
            <div class="ed-lis">
                <div class="ed-lis-key">详细地址</div>
                <div class="ed-lis-val"><input placeholder="街道门牌、楼层房间号等信息" v-model="info.addressDetail" /></div>
            </div>
        </div>
        <div class="add-address-btn" @click="save">保存</div>
    </div>
</template>
<script>
    export default {
        name: 'addressList',
        props:{
            addressInfo:{
                type:Object,
                default:{}
            }
        },
        computed:{

        },
        watch:{

        },
        data() {
            return {
                info:{}
            }
        },
        mounted(){
            this.doData();
        },
        methods:{
            //处理数据
            doData(){
                this.info = {
                    ...this.addressInfo,
                    provinceArea:this.addressInfo.province+this.addressInfo.city+this.addressInfo.county
                };
            },
            //保存点击
            save(){
                this.$emit('save',this.info);
            }
        }
    }
</script>
<style scoped>
    .address{overflow: hidden;}
    .edit{height: auto;overflow: hidden;}
    .add-address-btn{height: 40px;line-height: 40px;text-align: center;background:#ee0a24;color:#fff;font-size: 14px;
        border-radius: 20px;cursor:pointer;margin-top:10px;
    }
    .ed-lis{height: auto;overflow: hidden;}
    .ed-lis-key{width: 100px;height: 40px;line-height: 40px;font-size: 14px;color:#333;float:left;}
    .ed-lis-val{margin-left:100px;overflow: hidden;height: auto;}
    .ed-lis-val input{outline: none;font-size: 14px;color:#333;height: 20px;margin-top:10px;border:none;
        width: 100%;line-height: 20px;
    }
</style>


